.section {
  padding: 4rem 1.5rem;
  background: transparent;
}

.container {
  max-width: 800px;
  /* Reduced max-width for "smaller" look */
  margin: 0 auto;
}

.statsBanner {
  text-align: center;
  margin-bottom: 1.5rem;
  /* Reduced from 3rem */
  padding-bottom: 1.5rem;
  /* Reduced from 3rem */
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* ... skipped intermediate lines if any ... */

.imageContainer {
  width: 100%;
  max-height: 250px;
  /* Reduced from 400px */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
}

.image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.caption {
  padding: 1rem;
  text-align: center;
}

.author {
  font-weight: 700;
  color: #fff;
  font-size: 1rem;
}

.quote {
  font-size: 0.9rem;
  color: #ccc;
  font-style: italic;
  margin-top: 0.25rem;
}

.dots {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #444;
  border: none;
  cursor: pointer;
}

.activeDot {
  background: var(--color-accent-yellow, #FFD700);
}

@media (max-width: 1000px) {
  .statsText {
    font-size: 1.5rem;
  }
}