.badge {
  display: inline-block;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #fff;
  margin-bottom: 1.5rem;
  backdrop-filter: blur(10px);
}

/* Ensure spacing is better */
.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 2rem;
  z-index: 2;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6rem;
  /* Increase gap to separate text/image */
  align-items: center;
}

.headline {
  font-size: 3.5rem;
  /* Slightly smaller for better fit */
  font-weight: 800;
  line-height: 1.1;
  color: #fff;
  margin-bottom: 1.5rem;
}

.subHeadlineMain {
  font-size: 1.5rem;
  font-weight: 500;
  color: #ccc;
  margin-bottom: 2rem;
  line-height: 1.5;
}

.description {
  font-size: 1.125rem;
  color: #aaa;
  line-height: 1.6;
  margin-bottom: 2.5rem;
  max-width: 90%;
}

